import React from 'react'
import sginnovateLogo from '../../../assets/img/sginnovate-logo.png'
import procareLogo from '../../../assets/img/procare-logo.png'
import sarmentLogo from '../../../assets/img/sarment-logo.png'
import whizpaceLogo from '../../../assets/img/whizpace-logo.png'
import ExperienceCard from './ExperienceCard/ExperienceCard'
import './Experience.css'

const Experience = () => {

    return (
        <section id='experience' className='bg-floral-white px-1 py-6'>
            <h1 className='fs-1 fw-bold text-center mb-3'>Experience</h1>
            <div className='fl fl-col auto-w-centered'>
                <ExperienceCard
                    img={{ src: sginnovateLogo, alt: 'sginnovate logo' }}
                    title={'Software Engineering Intern, SGInnovate'}
                    period={'Dec 2021 - Feb 2022'}
                    accomplishments={[
                        'Proposed and implemented auto filling of resume details on Talent Programs (PX) and Talent Marketplace forms using Sovren API and NodeJS',
                        'Assisted in UAT with internal users department for release of 2022 Summation and PX runs',
                        'Updated PX and Talent marketplace forms based on user requirements using VueJS, NuxtJS',
                        'Tracking & closing of user issues and coordination with tech team in resolving issues using Jira',
                        'Implemented python script to automate daily tasks'
                    ]} />
                <ExperienceCard
                    img={{ src: procareLogo, alt: 'sarment logo' }}
                    title={'Software Engineering Intern, Procare Solution Pte Ltd.'}
                    period={'Mar 2021 - Jul 2021'}
                    accomplishments={[
                        'Led development of API for medication refill/partial order web service for HealthHub',
                        'Involved in virtual meetings and liaised with clients on technical specifications.',
                        'Involved in discussions on overall project architecture and designing of database schemas',
                        'Implemented 2-way SSL between API and test client'
                    ]} />
                <ExperienceCard
                    img={{ src: sarmentLogo, alt: 'sarment logo' }}
                    title={'Accounting Intern, Sarment Holding Ltd.'}
                    period={'Mar 2019'}
                    accomplishments={[
                        'Assisted the Consolidation and Compliance Manager in preparation of monthly management reports',
                        'Assisted in preparation of monthly group exchange rates',
                        'Preparation of various ad-hoc reports'
                    ]} />
                <ExperienceCard
                    img={{ src: whizpaceLogo, alt: 'whizpace logo' }}
                    title={'Product Testing Engineering Intern, Whizpace Pte Ltd.'}
                    period={'Jan 2019 - Feb 2019'}
                    accomplishments={[
                        'Testing of WhizMesh product using Bash',
                        'Troubleshooting and resolving bugs on GUI of WhizMesh using PHP'
                    ]} />
            </div>
        </section>
    )
}

export default Experience